    @include breakpoint(large){
        font-size: 3rem;
    }
}
 */
/* HOW TO USE IT

If you want a 30px h1 tag:

---
h1 {
    font-size: rem(30);
}
---

and it will output:

---
h1 {
    font-size: 1.875rem;
}
---

*/
.card {
  padding: 1.25rem;
}

.toggle__button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--toggle);
}